import modelExtend from 'dva-model-extend'
import { queryAreaList, changeAreaTitle, queryAddArea, deleteArea } from '../../services/song'
import { pageModel } from '../../models/common'
// import queryString from 'query-string'
import { message } from 'antd'

interface Ipagination {
  showSizeChanger: boolean,
  showQuickJumper: boolean,
  showTotal: string,
  current: number,
  total: number,
  pageSize: number
}
interface Idetail {
  list: any[],
  pagination: Ipagination
}
interface Istate {
  currentItem: any,
  modalVisible: boolean,
  detailVisible: boolean,
  detail: Idetail
}

export default modelExtend(pageModel, {
  namespace: 'area',
  state: {
    currentItem: {},
    modalVisible: false,
    detailVisible: false,
    // searchValue: '',
    detail: {
      list: [],
      pagination: {
        showSizeChanger: true,
        showQuickJumper: true,
        showTotal: (total: string) => `共 ${total} 条数据`,
        current: 1,
        total: 0,
        pageSize: 10,
      },
    },
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/song/area') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              // ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put }: any) {
      console.log('2323')
      // const searchValue = yield select(state => state.vote.searchValue)
      const data = yield call(queryAreaList, payload)
      if (data.code === 0) {
        const { topic } = data.data
        yield put({
          type: 'querySuccess',
          payload: {
            list: topic.data,
            pagination: {
              current: Number(payload.current_page) || 1,
              pageSize: Number(payload.per_page) || 10,
              total: topic.total,
            },
          },
        })
      } else {
        throw data
      }
    },
    * updataTitle ({ payload }: any, { call, put }: any) {
      console.log('2323')
      // const searchValue = yield select(state => state.vote.searchValue)
      const data = yield call(changeAreaTitle, payload)
      if (data.code === 0) {
        message.success('修改成功')
        yield put({
          type: 'query',
          payload: {
          },
        })
      } else {
        throw data
      }
    },
    * addArea ({ payload }: any, { call, put }: any) {
      // console.log('2323')
      // const searchValue = yield select(state => state.vote.searchValue)
      const data = yield call(queryAddArea, payload)
      if (data.code === 0) {
        message.success('添加成功')
        yield put({
          type: 'query',
          payload: {
          },
        })
      } else {
        throw data
      }
    },
    * deleteArea ({ payload }: any, { call, put }: any) {
      console.log('2323')
      // const searchValue = yield select(state => state.vote.searchValue)
      const data = yield call(deleteArea, payload)
      if (data.success) {
        message.success('删除成功')
        yield put({
          type: 'query',
          payload: {
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



